






















import { Component, Vue, Watch } from 'vue-property-decorator';
import { TodoListItemInfo } from '../modules/interfaces';
import store from '../modules/store';
import Global from '../modules/Global';

@Component({})
export default class TodoList extends Vue {
    /** 输入内容 */
    private inputText: string = '';

    /** 列表数据 */
    private list = store.todoList;

    @Watch('list', {
        deep: true
    }) onListChange() {
        store.saveTodoList();
    }

    /** 增加一条列表 */
    private addItem() {
        if (!this.inputText) return Global.openDialog({
            type: 'alert',
            content: '输入不能为空'
        });
        const item: TodoListItemInfo = {
            id: store.todoId,
            text: this.inputText,
            active: true,
        }
        store.todoId++;
        this.list.push(item);
        this.inputText = '';
    }

    /**
     * 切换 item 状态
     * @param index item索引
     */
    private toggleItem(index: number) {
        this.list[index].active = !this.list[index].active;
    }

    /**
     * 删除 item
     * @param e 事件
     * @param index item索引
     */
    private removeItem(e: Event, index: number) {
        // 防止和 toggleItem 事件冲突
        e.stopPropagation();
        e.preventDefault();
        this.list.splice(index, 1);
    }

    /** 删除第一条 */
    private removeFirst() {
        if (!this.list.length) return Global.openDialog({
            type: 'alert',
            content: '当前没有可以删除的列表'
        }); 
        this.list.shift();
    }

    /** 删除最后一条 */
    private removeLast() {
        if (!this.list.length) return Global.openDialog({
            type: 'alert',
            content: '当前没有可以删除的列表'
        });
        this.list.pop();
    }
}
